@import "vars";

.header {
  background-color: $c-blue;
  border-bottom-left-radius: 200px;
  height: 800px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  &__container {

    width: calc(100% - 228px);
    height: calc(100% - 49px);
  }

  &__nav {
    height: 26px;
    margin-right: 228px;
    display: flex;
  }
}
